// frontend/src/components/ErrorReportingPanel.js
import { useState } from 'react';
import toast from '../utils/toast';
import './ErrorReportingPanel.css';

const ErrorReportingPanel = ({ apiBaseUrl, onClose }) => {
    const [errorReport, setErrorReport] = useState({
        title: '',
        description: '',
        severity: 'medium', // 'low', 'medium', 'high', 'critical'
        category: 'bug', // 'bug', 'feature', 'performance', 'ui', 'crash'
        steps_to_reproduce: '',
        expected_behavior: '',
        actual_behavior: '',
        browser: navigator.userAgent,
        screen_resolution: `${window.screen.width}x${window.screen.height}`,
        url: window.location.href,
        console_errors: []
    });
    const [submitting, setSubmitting] = useState(false);
    const [attachments, setAttachments] = useState([]);

    const captureConsoleErrors = () => {
        // Capture last console errors from window if available
        const errors = window.consoleErrors || [];
        setErrorReport(prev => ({ ...prev, console_errors: errors }));
        toast.info('‚ÑπÔ∏è Konsol hatalarƒ± yakalandƒ±');
    };

    const submitErrorReport = async () => {
        if (!errorReport.title.trim()) {
            toast.error('‚ùå Ba≈ülƒ±k gerekli');
            return;
        }

        if (!errorReport.description.trim()) {
            toast.error('‚ùå A√ßƒ±klama gerekli');
            return;
        }

        try {
            setSubmitting(true);
            const token = localStorage.getItem('access_token');
            const response = await fetch(`${apiBaseUrl}/errors/report/`, {
                method: 'POST',
                headers: {
                    'Authorization': `Bearer ${token}`,
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(errorReport)
            });

            if (response.ok) {
                const data = await response.json();
                toast.success('‚úÖ Hata raporu g√∂nderildi');
                setErrorReport({
                    title: '',
                    description: '',
                    severity: 'medium',
                    category: 'bug',
                    steps_to_reproduce: '',
                    expected_behavior: '',
                    actual_behavior: '',
                    browser: navigator.userAgent,
                    screen_resolution: `${window.screen.width}x${window.screen.height}`,
                    url: window.location.href,
                    console_errors: []
                });
                setTimeout(() => onClose(), 1500);
            } else {
                const error = await response.json();
                toast.error(`‚ùå ${error.error || 'G√∂nderim ba≈üarƒ±sƒ±z'}`);
            }
        } catch (error) {
            console.error('Submit error report error:', error);
            toast.error('‚ùå Baƒülantƒ± hatasƒ±');
        } finally {
            setSubmitting(false);
        }
    };

    const getSeverityColor = (severity) => {
        const colors = {
            'low': '#3b82f6',
            'medium': '#faa61a',
            'high': '#ff6b61',
            'critical': '#ff3b30'
        };
        return colors[severity] || colors.medium;
    };

    const getCategoryIcon = (category) => {
        const icons = {
            'bug': 'üêõ',
            'feature': '‚ú®',
            'performance': '‚ö°',
            'ui': 'üé®',
            'crash': 'üí•'
        };
        return icons[category] || '‚ùì';
    };

    return (
        <div className="error-reporting-overlay" onClick={onClose}>
            <div className="error-reporting-panel" onClick={e => e.stopPropagation()}>
                <div className="error-reporting-header">
                    <h2>üêõ Hata Bildirimi</h2>
                    <button className="close-btn" onClick={onClose}>‚úï</button>
                </div>

                <div className="error-reporting-content">
                    <div className="info-banner">
                        <span className="info-icon">üí°</span>
                        <p>Kar≈üƒ±la≈ütƒ±ƒüƒ±nƒ±z hatalarƒ± detaylƒ± bir ≈üekilde bildirerek uygulamayƒ± geli≈ütirmemize yardƒ±mcƒ± olabilirsiniz.</p>
                    </div>

                    <div className="form-section">
                        <div className="form-group">
                            <label>Ba≈ülƒ±k *</label>
                            <input
                                type="text"
                                className="form-input"
                                placeholder="Kƒ±sa ve a√ßƒ±klayƒ±cƒ± bir ba≈ülƒ±k"
                                value={errorReport.title}
                                onChange={(e) => setErrorReport({ ...errorReport, title: e.target.value })}
                            />
                        </div>

                        <div className="form-row">
                            <div className="form-group">
                                <label>Kategori</label>
                                <select
                                    className="form-select"
                                    value={errorReport.category}
                                    onChange={(e) => setErrorReport({ ...errorReport, category: e.target.value })}
                                >
                                    <option value="bug">üêõ Bug (Hata)</option>
                                    <option value="feature">‚ú® Feature Request (√ñzellik ƒ∞steƒüi)</option>
                                    <option value="performance">‚ö° Performance (Performans)</option>
                                    <option value="ui">üé® UI/UX (Aray√ºz)</option>
                                    <option value="crash">üí• Crash (√á√∂kme)</option>
                                </select>
                            </div>

                            <div className="form-group">
                                <label>√ñnem Derecesi</label>
                                <select
                                    className="form-select"
                                    value={errorReport.severity}
                                    onChange={(e) => setErrorReport({ ...errorReport, severity: e.target.value })}
                                >
                                    <option value="low">üü¢ D√º≈ü√ºk</option>
                                    <option value="medium">üü° Orta</option>
                                    <option value="high">üü† Y√ºksek</option>
                                    <option value="critical">üî¥ Kritik</option>
                                </select>
                            </div>
                        </div>

                        <div className="form-group">
                            <label>A√ßƒ±klama *</label>
                            <textarea
                                className="form-textarea"
                                placeholder="Hatayƒ± detaylƒ± bir ≈üekilde a√ßƒ±klayƒ±n..."
                                value={errorReport.description}
                                onChange={(e) => setErrorReport({ ...errorReport, description: e.target.value })}
                                rows={5}
                            />
                        </div>

                        <div className="form-group">
                            <label>Hatayƒ± Yeniden Olu≈üturma Adƒ±mlarƒ±</label>
                            <textarea
                                className="form-textarea"
                                placeholder="1. ƒ∞lk adƒ±m&#10;2. ƒ∞kinci adƒ±m&#10;3. √ú√ß√ºnc√º adƒ±m"
                                value={errorReport.steps_to_reproduce}
                                onChange={(e) => setErrorReport({ ...errorReport, steps_to_reproduce: e.target.value })}
                                rows={4}
                            />
                        </div>

                        <div className="form-row">
                            <div className="form-group">
                                <label>Beklenen Davranƒ±≈ü</label>
                                <textarea
                                    className="form-textarea"
                                    placeholder="Ne olmasƒ±nƒ± bekliyordunuz?"
                                    value={errorReport.expected_behavior}
                                    onChange={(e) => setErrorReport({ ...errorReport, expected_behavior: e.target.value })}
                                    rows={3}
                                />
                            </div>

                            <div className="form-group">
                                <label>Ger√ßekle≈üen Davranƒ±≈ü</label>
                                <textarea
                                    className="form-textarea"
                                    placeholder="Ne oldu?"
                                    value={errorReport.actual_behavior}
                                    onChange={(e) => setErrorReport({ ...errorReport, actual_behavior: e.target.value })}
                                    rows={3}
                                />
                            </div>
                        </div>

                        <div className="system-info">
                            <h4>üìä Sistem Bilgileri</h4>
                            <div className="info-grid">
                                <div className="info-item">
                                    <span className="info-label">Tarayƒ±cƒ±:</span>
                                    <span className="info-value">{navigator.userAgent.split('(')[1]?.split(')')[0] || 'Unknown'}</span>
                                </div>
                                <div className="info-item">
                                    <span className="info-label">Ekran:</span>
                                    <span className="info-value">{errorReport.screen_resolution}</span>
                                </div>
                                <div className="info-item">
                                    <span className="info-label">URL:</span>
                                    <span className="info-value truncate">{errorReport.url}</span>
                                </div>
                            </div>
                            <button 
                                className="capture-errors-btn"
                                onClick={captureConsoleErrors}
                            >
                                üìù Konsol Hatalarƒ±nƒ± Yakala
                            </button>
                        </div>

                        <div className="preview-section">
                            <h4>üëÅÔ∏è √ñnizleme</h4>
                            <div className="error-preview">
                                <div className="preview-header">
                                    <span className="preview-icon">{getCategoryIcon(errorReport.category)}</span>
                                    <span className="preview-title">{errorReport.title || 'Ba≈ülƒ±k'}</span>
                                    <span 
                                        className="preview-severity"
                                        style={{ backgroundColor: getSeverityColor(errorReport.severity) }}
                                    >
                                        {errorReport.severity}
                                    </span>
                                </div>
                                <div className="preview-body">
                                    {errorReport.description || 'A√ßƒ±klama burada g√∂r√ºnecek...'}
                                </div>
                            </div>
                        </div>

                        <div className="form-actions">
                            <button 
                                className="cancel-btn"
                                onClick={onClose}
                            >
                                ƒ∞ptal
                            </button>
                            <button 
                                className="submit-btn"
                                onClick={submitErrorReport}
                                disabled={submitting}
                            >
                                {submitting ? '‚è≥ G√∂nderiliyor...' : 'üì® Raporu G√∂nder'}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default ErrorReportingPanel;
